import React from 'react';
import './Noticia.css';

const Noticia = (props) => {
  return (
    <div className="Noticia">
      <img className="card-img-top imagem" src={props.local} alt="Imagem"></img>
      <div>
        <h4 className="card-text texto">{props.texto}</h4>
      </div>
    </div>
  );
};

export default Noticia;



