import React, { Component } from 'react';
import './App.css';
import Dvd from './DivInicial/DivInicial';
import Nota from './Nota/Nota';
import Radium from 'radium';
import Ntc from './Noticia/Noticia'
import Titulo from './Titulo/Titulo';

