  state = {
    mostrar: true,
    quemSouEu: true,
    portfolio: false,
    blog: false,
    contato: false,
    modoNoturno: false
  }

  mostrarNota = () => {
    const showDiv = this.state.mostrar;
    this.setState({
      mostrar: !showDiv
    })
  };

  carregarPrimeiraPagina = () => {
    const primeiraPagina = this.state.quemSouEu;
    if ( !primeiraPagina ) {
      this.setState({
        quemSouEu : !primeiraPagina,
        portfolio: false,
        blog: false,
        contato: false
      });
    }
  };

  carregarSegundaPagina = () => {
    const segundaPagina = this.state.portfolio;
    if ( !segundaPagina ) {
      this.setState({
        quemSouEu : false,
        portfolio: !segundaPagina,
        blog: false,
        contato: false
      });
    }
  }

  carregarTerceiraPagina = () => {
    const terceiraPagina = this.state.blog;
    if ( !terceiraPagina ) {
      this.setState({
        quemSouEu : false,
        portfolio: false,
        blog: !terceiraPagina,
        contato: false
      });
    }
  }

  carregarQuartaPagina = () => {
    const quartaPagina = this.state.contato;
    if ( !quartaPagina ) {
      this.setState({
        quemSouEu : false,
        portfolio: false,
        blog: false,
        contato: !quartaPagina
      });
    }
  }

  alternarModoNoturno = () => {
    const diaNoite = this.state.modoNoturno;
    this.setState({
      modoNoturno: !diaNoite
    })
  }

