import React, { Component } from 'react';
import './Nota.css';

class Nota extends Component {
  render() {
    const classes = ["Nota", this.props.classe].join(' ');
    return (
      <div className={classes} onClick={this.props.click}>
        <h4 className="notaTitulo"><i className={this.props.icone} aria-hidden="true"></i> {this.props.name}</h4>
        <p>{this.props.nota}</p>
      </div>
    );
  }
}

export default Nota;



