import React from 'react';
import './DivInicial.css';

const DivInicial = () => {
  return (
    <div className="DivInicial">
      <h1>Blog Pessoal. <i className="fa fa-smile-o" aria-hidden="true"></i></h1>
      <p className="parInicial">Olá, aqui você pode encontrar artigos úteis sobre o aprendizado de alguns conteúdos, e se tiver um tempo
      livre e quiser gastar um pouco de seu tempo, pode me conhecer melhor.</p>
    </div>
  );
};


export default DivInicial;



